*,
*:before,
*:after {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
  scroll-behavior: smooth;
}

body {
  font-family: "League Spartan", system-ui;
  font-size: 1.6rem;
  min-height: 100%;
  color: #000000;
  background-color: #190b2d;
}

.container {
  width: 95%;
  max-width: 1350px;
  margin: 0 auto;
}

.grid {
  display: grid;
  gap: 1.5rem;
}

.section {
  padding-block: 1rem 2rem;
}

.container-sm {
  max-width: 100rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

ul {
  list-style: none;
}

.main {
  overflow: hidden;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-thumb {
  background-color: #80d9ea;
}

::-webkit-scrollbar-track {
  background-color: #254176;
}

#particles-js {
  background-color: #190b2d;
}

.main-title {
  font-family: "Purple Purse", system-ui;
  font-size: 32px;
  background: linear-gradient(0deg, rgb(146, 139, 232) 40%, rgb(130, 207, 234) 65%, rgb(128, 217, 234) 74%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-align: center;
}

.secondary-title {
  font-family: "Neucha", cursive;
  font-size: 24px;
  background: linear-gradient(0deg, rgb(36, 151, 209) 9%, rgb(68, 148, 216) 19%, rgb(137, 148, 217) 37%, rgb(186, 148, 218) 60%, rgb(230, 158, 202) 90%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-align: center;
}

