#home {
  padding-top: 100px;
  margin-top: -100px;
}

.header {
  min-height: calc(100vh + 100px);
  overflow: hidden;
}
.header__wave {
  display: none;
}
@media (min-width: 1024px) {
  .header__wave {
    display: block;
    position: absolute;
    bottom: -120px;
    left: 0;
    width: 100%;
    height: auto;
    z-index: 1;
  }
}
.header__container {
  position: relative;
  z-index: 2;
  color: #ffffff;
  margin: 12rem auto 0 auto;
}
.header__title {
  text-align: center;
  font-family: "Purple Purse", system-ui;
  font-size: 36px;
  line-height: 30px;
}
.header__title--secondary {
  color: #928be8;
}
.header__title--main {
  color: #80d9ea;
  line-height: 4rem;
}
.header__information {
  margin-top: 5rem;
  text-align: center;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
  justify-items: center;
}
@media (min-width: 1024px) {
  .header__information {
    place-items: center;
    margin: 10rem;
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
  }
}
.header__description {
  font-family: "League Spartan", system-ui;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}
@media (min-width: 1024px) {
  .header__description {
    margin: 0 auto;
    padding: 2rem;
    justify-content: flex-start;
    align-items: flex-start;
  }
}
.header__description--title {
  font-family: "Pixelify Sans", system-ui;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  font-size: 25px;
  text-align: center;
}
@media (min-width: 1024px) {
  .header__description--title {
    flex-direction: row;
  }
}
@media (min-width: 768px) {
  .header__description--title {
    font-size: 32px;
  }
}
.header__description--ima {
  color: #ffffff;
  margin: 2rem 0 0 0;
}
@media (min-width: 1024px) {
  .header__description--ima {
    margin: 0 1rem 0 0;
  }
}
.header__description--animation {
  margin: 0 0 0 0;
  text-align: center;
}
.header__description--animation span {
  position: relative;
}
.header__description--animation span::before {
  content: "Systems Engineer";
  background: linear-gradient(0deg, rgb(146, 139, 232) 9%, rgb(134, 190, 233) 35%, rgb(130, 207, 234) 65%, rgb(128, 217, 234) 74%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: words 20s infinite;
}
.header__description--animation span::after {
  content: "";
  background-color: #190b2d;
  position: absolute;
  width: calc(100% + 8px);
  height: calc(100% + 8px);
  border-left: 5px solid #190b2d;
  right: -5px;
  animation: typing 20s steps(14) infinite;
}
.header__description--text {
  margin-top: 3rem;
  font-size: 20px;
  color: #ffffff;
  margin-bottom: 2rem;
  text-align: center;
}
@media (min-width: 1024px) {
  .header__description--text {
    text-align: left;
  }
}
.header__description--buttons {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 280px;
}
@media (min-width: 768px) {
  .header__description--buttons {
    width: 350px;
  }
}
.header__description--button, .header__description--button-social {
  text-align: center;
  background: linear-gradient(0deg, rgb(36, 151, 209) 9%, rgb(68, 148, 216) 19%, rgb(137, 148, 217) 37%, rgb(186, 148, 218) 60%, rgb(230, 158, 202) 90%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.header__description--button {
  font-size: 20px;
  transition: all 0.3s ease-in-out;
}
.header__description--button:hover {
  font-size: 22px;
}
.header__description--button-social {
  width: 27px;
  transition: width 0.3s ease-in-out;
}
.header__description--button-social:hover {
  width: 30px;
}
.header__description--button-line {
  background: linear-gradient(170deg, rgb(128, 217, 234) 30%, rgb(146, 139, 232) 80%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

@keyframes words {
  0%, 20% {
    content: "Systems Engineer";
  }
  21%, 40% {
    content: "Chemical Engineer";
  }
  41%, 60% {
    content: "Data Analyst";
  }
  61%, 80% {
    content: "Web Developer";
  }
  81%, 100% {
    content: "Leader";
  }
}
@keyframes typing {
  10%, 15%, 30%, 35%, 50%, 55%, 70%, 75%, 90%, 95% {
    width: 0;
  }
  5%, 20%, 25%, 40%, 45%, 60%, 65%, 80%, 85% {
    width: calc(100% + 10px);
  }
}
.scroll-down__container {
  cursor: pointer;
}
@media (min-width: 1024px) {
  .scroll-down__container {
    position: absolute;
    top: 300px;
  }
}

#about {
  padding-top: 60px;
}
@media (min-width: 768px) {
  #about {
    padding-top: 70px;
  }
}
@media (min-width: 1024px) {
  #about {
    padding-top: 100px;
  }
}

.about {
  min-height: 100vh;
  overflow: hidden;
}
.about__title--text {
  color: #ffffff;
  text-align: center;
}
@media (min-width: 1024px) {
  .about__title--text {
    font-size: 24px;
  }
}
.about__grid {
  padding-top: 3rem;
  place-items: center;
  margin: 0 auto;
  padding-bottom: 2rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
  justify-items: center;
}
@media (min-width: 1024px) {
  .about__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
    padding-top: 10rem;
    max-width: 1200px;
  }
}
.about__img {
  margin-top: 2rem;
  max-width: 450px;
}
@media (min-width: 1024px) {
  .about__img {
    width: 400px;
  }
}
.about__description {
  font-family: "League Spartan", system-ui;
  color: #ffffff;
  font-size: 18px;
  text-align: center;
  margin: 1.5rem;
}
@media (min-width: 1024px) {
  .about__description {
    text-align: left;
    font-size: 24px;
  }
}
.about__description--text span {
  background: linear-gradient(0deg, rgb(146, 139, 232) 9%, rgb(134, 190, 233) 35%, rgb(130, 207, 234) 65%, rgb(128, 217, 234) 74%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.about__description--button {
  display: flex;
  justify-content: center;
  font-family: "Neucha", cursive;
  font-size: 28px;
  background: linear-gradient(0deg, rgb(36, 151, 209) 9%, rgb(68, 148, 216) 19%, rgb(137, 148, 217) 37%, rgb(186, 148, 218) 60%, rgb(230, 158, 202) 90%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

#career {
  padding-top: 60px;
}
@media (min-width: 768px) {
  #career {
    padding-top: 70px;
  }
}
@media (min-width: 1024px) {
  #career {
    padding-top: 100px;
  }
}

.career {
  min-height: 100vh;
  overflow: hidden;
}
.career__timeline {
  background-color: rgba(138, 94, 196, 0.062745098);
  backdrop-filter: blur(0.4rem);
  padding: 0 3rem 2rem 4rem;
  border-radius: 2rem;
  margin: 3rem auto;
}
@media (min-width: 768px) {
  .career__timeline {
    max-width: 120rem;
  }
}
.career__timeline--information {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .career__timeline--information {
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
}
@media (min-width: 1024px) {
  .career__timeline--title {
    width: 300px;
  }
}
@media (min-width: 768px) {
  .career__timeline--description {
    margin-left: 2rem;
    max-width: 60rem;
  }
}
.career__timeline--position {
  font-family: "Purple Purse", system-ui;
  background: linear-gradient(170deg, rgb(128, 217, 234) 30%, rgb(146, 139, 232) 80%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.career__timeline--positionedu {
  font-family: "Purple Purse", system-ui;
  background: linear-gradient(0deg, rgb(36, 151, 209) 9%, rgb(68, 148, 216) 19%, rgb(137, 148, 217) 37%, rgb(186, 148, 218) 60%, rgb(230, 158, 202) 90%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.career__timeline--site {
  font-family: "Neucha", cursive;
}
.career__timeline--text {
  font-family: "League Spartan", system-ui;
  font-size: 1.5rem;
}
@media (min-width: 1024px) {
  .career__timeline--text {
    font-size: 2rem;
  }
}
.career__accordion--title {
  font-family: "Neucha", cursive;
  background: linear-gradient(170deg, rgb(128, 217, 234) 30%, rgb(146, 139, 232) 80%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.career__accordion--info {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .career__accordion--info {
    flex-direction: row;
    justify-content: space-between;
  }
}
.career__modal--bg {
  background-color: rgba(59, 23, 105, 0.3137254902);
  backdrop-filter: blur(0.4rem);
}
.career__modal--img {
  max-width: 300px;
}

#accordion-flush {
  width: 100%;
}

#projects {
  padding-top: 60px;
}
@media (min-width: 768px) {
  #projects {
    padding-top: 70px;
  }
}
@media (min-width: 1024px) {
  #projects {
    padding-top: 100px;
  }
