.work {
  min-height: 100vh;
  overflow: hidden;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
  justify-items: center;
}
.work__controls {
  margin-bottom: 2rem;
  display: flex;
  justify-content: center;
}
.work__controls--buttons {
  border: solid 2px #ffffff;
  text-align: center;
  display: inline-block;
  width: auto;
  margin: 0 auto;
}
.work__control {
  cursor: pointer;
  background-color: transparent;
  border: none;
  font-family: "League Spartan", system-ui;
  color: #ffffff;
  font-size: 15px;
  padding: 0.5rem;
}
@media (min-width: 768px) {
  .work__control {
    padding: 1rem;
  }
}
.work__control.blue {
  color: #8ecae6;
}
.work__control.orange {
  color: #f07167;
}
.work__control.pink {
  color: #d4acd0;
}
.work__control.yellow {
  color: #e3c37f;
}
.work__img {
  width: 250px;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.17) 0px -23px 25px 0px inset, rgba(0, 0, 0, 0.15) 0px -36px 30px 0px inset, rgba(0, 0, 0, 0.1) 0px -79px 40px 0px inset, rgba(0, 0, 0, 0.06) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;
}
.work__project--cover {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}
.work__project--img-container {
  background: linear-gradient(135deg, rgba(146, 139, 232, 0.5647058824) 0%, rgba(162, 181, 240, 0.5647058824) 50%, rgba(128, 217, 234, 0.5647058824) 100%);
