  height: 200px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.work__project--title {
  font-family: "Pixelify Sans", system-ui;
  margin: 0;
  background: linear-gradient(170deg, rgb(128, 217, 234) 30%, rgb(146, 139, 232) 80%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 28px;
}
.work__project--techs {
  margin-bottom: 3rem;
