  width: 100%;
  text-align: center;
  border-radius: 0.5rem;
  font-size: 12px;
}
.work__project--icon {
  width: 30px;
  height: 30px;
  margin: 1rem;
  color: #0f0023;
}
.work__project--laptop {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}
.work__projectdesc--container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.work__projectdesc--title {
  text-align: center;
  margin: 0;
  font-family: "Purple Purse", system-ui;
  background: linear-gradient(0deg, rgb(146, 139, 232) 40%, rgb(130, 207, 234) 65%, rgb(128, 217, 234) 74%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 22px;
}
.work__projectdesc--text {
  width: 180px;
  text-align: center;
  margin: 0.5rem;
  font-family: "League Spartan", system-ui;
  color: #ffffff;
}
.work__projectdesc--link {
  margin-top: 0.5rem;
  font-family: "Neucha", cursive;
  font-size: 20px;
  background: linear-gradient(0deg, rgb(36, 151, 209) 9%, rgb(68, 148, 216) 19%, rgb(137, 148, 217) 37%, rgb(186, 148, 218) 60%, rgb(230, 158, 202) 90%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.projects__grid {
  margin-bottom: 3rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
  justify-items: center;
}
@media (min-width: 768px) {
  .projects__grid {
    display: grid;
